export default {
	data() {
		return {
			merchantList: [],
			searchID: "",
			searchname: "",
			merchant: {},
			emit: 10,
			loadtext: "上拉加载更多",
		}
	},
	onLoad() {
		this.__init()
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
	},
	onReachBottom() {
		if(this.emit > this.merchantList.length) {
			console.log('不会再上拉了哦');
			return
		}
		this.loadtext = "加载中..."
		this.emit += 10 
		console.log("触发上拉加载", this.emit);
		this.__init()
	},
	methods: {
		all() {
			const data = uni.getStorageSync('earn')
			data.mer_id = ''
			data.mer_name = ''
			uni.setStorageSync('earn', data)
			uni.navigateBack();
		},
		select(item) {
			const data = uni.getStorageSync('earn')
			data.mer_id = item.merchant_id
			data.mer_name = item.merchant_id
			uni.setStorageSync('earn', data)
			uni.navigateBack();
		},
		async __init() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "merchant_list",
				slimit: 0,  //列表 起始值
				elimit: this.emit,  //列表  数量
				key_value: (this.searchID || this.searchname)
			}).then((data) => {
				uni.hideLoading()
				this.merchantList = data.arr
				if(this.merchantList.length < 1) {
					uni.showToast({
						title: "暂无数据",
						icon: "none",
						duration: 2500
					})
				}
				// 恢复加载状态
				this.loadtext = this.merchantList.length < this.emit ? "没有更多了" :  "上拉加载更多"
				uni.setStorageSync('list', data)
			}).catch(() => {
				console.log("catch error!!");
			})
		},
		searchbtn() {
			this.__init()
		}
	}
}
